'use strict';

// MAIN //

/**
* Polyfill for returning the current working directory in environments which do not have support for returning the current working directory.
*
* @private
* @returns {string} current working directory
*
* @example
* var dir = cwd();
* // returns '/'
*/
function cwd() {
	return '/';
}


// EXPORTS //
export default cwd;
